<template>
  <div class="phoneContainer">
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="goBack"
        @keyup="goBack"
        class="backImg"
        src="../assets/뒤로가기.png"
      />
      <h2 class="word1">음성 내용 안내!</h2>
    </div>
    <div class="btnLayout">
      <div class="barDiv">
        <img class="mainIcon2" src="../assets/볼륨.png" />
        <br />
        <input
          type="range"
          id="volume"
          v-model="volume"
          min="0"
          max="2.0"
          step="0.1"
        />
        <br />
        <div class="buttonGroup2">
          <button
            class="shortBtn2"
            @click="downVoice"
            :style="{ fontSize: '24px' }"
          >
            ▬
          </button>
          <button
            class="shortBtn2"
            @click="upVoice"
            :style="{ fontSize: '24px' }"
          >
            ✚
          </button>
        </div>
      </div>
      <div class="barDiv">
        <img class="mainIcon2" src="../assets/속도.png" />
        <br />
        <input
          type="range"
          id="rate"
          v-model="rate"
          min="0.1"
          max="1.5"
          step="0.1"
        />
        <br />
        <div class="buttonGroup2">
          <button
            class="shortBtn2"
            @click="slowVoice"
            :style="{ fontSize: '24px' }"
          >
            ◀◀
          </button>
          <button
            class="shortBtn2"
            @click="fastVoice"
            :style="{ fontSize: '24px' }"
          >
            ▶▶
          </button>
        </div>
      </div>

      <div class="btnBundles2">
        <button class="mainBtn" @click="speakText">
          <img class="mainIcon" src="../assets/재생.png" />
          <span class="mainTxt">재생</span>
        </button>
        <button class="mainBtn" @click="stopSpeech">
          <img class="mainIcon" src="../assets/정지.png" />
          <span class="mainTxt">정지</span>
        </button>
      </div>
    </div>

    <router-link to="/cs">
      <button class="longBtn1" @click="stopSpeech">요약확인</button>
    </router-link>
    <router-link to="/main">
      <button class="longBtn1" @click="stopSpeech">메인으로</button>
    </router-link>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "CameraVoice",
  components: {},
  data() {
    return {
      fileContent: null,
      currentTime: null,
      fontSize: 16,
      volume: 0.5, // 기본 볼륨
      rate: 1.0, // 기본 속도
    };
  },
  async mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
    try {
      const response = await axios.get("/purba1/out1.txt");
      this.fileContent = response.data;
    } catch (error) {
      console.error("텍스트 파일을 불러오는 중 에러 발생:", error);
    }
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    upVoice() {
      if (this.volume < 2) {
        this.volume = Math.min(this.volume + 0.1, 2);
      }
    },
    downVoice() {
      if (this.volume > 0) {
        this.volume = Math.max(this.volume - 0.1, 0);
      }
    },
    fastVoice() {
      if (this.rate < 1.5) {
        this.rate = Math.min(this.rate + 0.1, 1.5);
      }
    },
    slowVoice() {
      if (this.rate > 0.1) {
        this.rate = Math.max(this.rate - 0.1, 0.1);
      }
    },
    speakText() {
      if (!this.fileContent) return;

      const utterance = new SpeechSynthesisUtterance(this.fileContent);
      utterance.volume = this.volume; // 볼륨 조절
      utterance.rate = this.rate; // 속도 조절
      utterance.lang = "ko-KR"; // 한국어로 설정
      window.speechSynthesis.speak(utterance);
    },
    stopSpeech() {
      window.speechSynthesis.cancel(); // 현재 진행 중인 음성을 멈춤
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
